  .widget-phone {
    right: inherit;
  }
}
@media only screen and (max-width: 1180px) {
  .widget-phone {
    right: 15px;
  }
}
@media only screen and (max-width: 991px) {
  .widget-phone {
    right: inherit;
  }
}
@media only screen and (max-width: 940px) {
  .widget-phone {
    right: 15px;
  }
}
@media only screen and (max-width: 767px) {
  .widget-phone {
    display: none !important;
  }
}
.widget-phone:before {
  margin: auto;
  color: #fff;
  font-size: 33px;
  content: "\f879";
  font-family: "Font Awesome 5 Free";
  font-weight: 900;
  z-index: 1;
}
.widget-phone:after {
  content: "";
  position: absolute;
  width: 160px;
  height: 160px;
}
.widget-phone .widget2-circles {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
}
.widget-phone .widget2-circles:before, .widget-phone .widget2-circles:after {
  content: "";
  position: absolute;
  border-radius: 50%;
}
.widget-phone .widget2-circles:before {
  width: 160px;
  height: 160px;
  border: 2px solid #00aff2;
  opacity: 0.5;
  animation: widget-phone-circle-anim 2.2s infinite ease-in-out;
}
.widget-phone .widget2-circles:after {
  width: 100px;
  height: 100px;
  background-color: #00aff2;
  opacity: 0.2;
  animation: widget-phone-circle-fill-anim 2.3s infinite ease-in-out;
}
.widget-phone:hover {
  background-color: #75eb50;
  opacity: 1;
  animation: widget-phone-img-anim 1s infinite ease-in-out;
}
.widget-phone:hover .widget2-circles:before {
  border: 2px solid #75eb50;
  animation: widget-phone-circle-anim 1.2s infinite ease-in-out;
}
.widget-phone:hover .widget2-circles:after {
  background-color: #75eb50;
}

@keyframes widget-phone-img-anim {
  0% {
    transform: rotate(0deg);
  }
  10% {
    transform: rotate(-25deg);
  }
  20% {
    transform: rotate(25deg);
  }
  30% {
    transform: rotate(-25deg);
  }
  40% {
    transform: rotate(25deg);
  }
  50% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(0deg);
  }
}
@keyframes widget-phone-circle-fill-anim {
  0% {
    transform: scale(0.7);
  }
  50% {
    transform: scale(1);
  }
  100% {
    transform: scale(0.7);
  }
}
@keyframes widget-phone-circle-anim {
  0% {
    transform: scale(0.5);
    opacity: 0.1;
  }
  30% {
    transform: scale(0.7);
    opacity: 0.5;
  }
  100% {
    transform: scale(1);
    opacity: 0.1;
  }
}
